.ui-btn {
  display: inline-block;
  vertical-align: middle;
  height: 70px;
  line-height: 70px;
  max-width: 100%;
  position: relative;
  border-radius: 10px;
  padding: 0px 60px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background: linear-gradient(107.72deg, #D32142 7.05%, #BF3138 63.43%);
  box-shadow: 0px 0px 23px rgba(243, 63, 73, 0.33), inset 0px -4px 1px rgba(0, 0, 0, 0.27);
  color: #FFF;
  text-align: center;
  font-size: 16px;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: box-shadow 0.2s, transform 0.2s;
}
.ui-btn-block {
  display: block;
  width: 100%;
  padding: 0px 5px;
}
.ui-btn:hover {
  box-shadow: none;
  transform: translate(0px, 1px);
  color: #FFF;
}
.ui-btn:active {
  color: #FFF;
  transform: translate(0px, 2px);
}
.ui-btn-gray {
  background: linear-gradient(0deg, #8D829D, #8D829D), linear-gradient(107.72deg, #8D8D8D 7.05%, #707070 63.43%);
  box-shadow: inset 0px -4px 1px rgba(0, 0, 0, 0.27);
}
@media (max-width: 599px) {
  .ui-btn {
    height: 60px;
    line-height: 60px;
    padding: 0px 45px;
  }
}

