.screens {
  max-width: 2560px;
  margin: 0 auto;
  max-height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
}
@media (max-width: 992px) {
  .screens {
    max-height: calc(var(--vh, 1vh) * 100);
  }
}

.screen {
  scroll-snap-align: start;
}

.screen-promo {
  height: 100vh;
  max-height: 1366px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  background-color: #B9B1BB;
  padding-top: 150px;
  padding-top: min(15vh, 150px);
  padding-left: 180px;
  padding-left: min(9.375vw, 180px);
}
.screen-promo-content {
  color: #2D2835;
  max-width: 560px;
}
.screen-promo-logo {
  margin-bottom: 50px;
}
.screen-promo-label {
  font-size: 48px;
  font-size: 3rem;
  font-weight: 500;
  line-height: 1.2;
  font-family: "Accia Flare";
}
.screen-promo-bg {
  position: absolute;
  z-index: -2;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  object-fit: cover;
  object-position: center;
}
.screen-promo-bottles {
  position: absolute;
  z-index: -1;
  right: 0;
  bottom: 0;
  max-height: 100%;
  max-width: 85%;
  display: block;
}
