  .screen-promo-bottles {
    max-width: 75%;
  }
}
@media (orientation: portrait) {
  .screen-promo-bottles {
    max-width: 100%;
  }
}
.screen-promo-appeal {
  max-width: 156px;
  position: absolute;
  bottom: 0px;
  left: 50%;
  transform: translate(-50%, 50%);
  cursor: pointer;
  z-index: 10;
}
.screen-promo-appeal-rotate {
  animation: rotate 12s linear infinite;
  transition: opacity 0.35s;
}
.screen-promo-appeal-arrow {
  position: absolute;
  left: 0;
  right: 0;
  top: 30px;
  margin: 0 auto;
  transition: transform 0.35s;
}
.screen-promo-appeal:hover .screen-promo-appeal-arrow {
  transform: translateY(3px);
